import { trackCustomEvent, CustomEventArgs } from 'gatsby-plugin-google-analytics';

const track = (args: CustomEventArgs) => {
  console.table(args);
  return trackCustomEvent(args);
};

const getState = (logger: string) => () =>
  track({
    category: logger,
    action: 'state',
  });

const getClick = (logger: string) => (logName: string) =>
  track({
    category: logger,
    action: 'click',
    label: logName,
  });

const getImpression = (logger: string) => (logName: string) =>
  track({
    category: logger,
    action: 'impression',
    label: logName,
  });

const getCustomEvent = (logger: string) => (logName: string, eventName: string) =>
  track({
    category: logger,
    action: eventName,
    label: logName,
  });

export interface Logger {
  state: ReturnType<typeof getState>;
  click: ReturnType<typeof getClick>;
  impression: ReturnType<typeof getImpression>;
  event: ReturnType<typeof getCustomEvent>;
}
export const generateLogger = (logger: string): Logger => {
  return {
    state: getState(logger),
    click: getClick(logger),
    impression: getImpression(logger),
    event: getCustomEvent(logger),
  };
};
